import React, { useEffect, useState } from 'react'
import "./Minigame.css"

export default function Minigame(props) {

    var myGamePiece;
    var myObstacles = [];
    var myScore;

    function startGame() {

        if (props.won === false) {
            myGamePiece = new objectENG(30, 30, "rgb(255,215,0)", 10, 120);
        }
        if (props.won === 1) {
            myGamePiece = new objectENG(70, 40, "rgb(255,215,0)", 10, 120);
        }


        myGamePiece.gravity = 0.05;
        myScore = new objectENG("30px", "Consolas", "black", 280, 40, "text");
        myGameArea.start();
    }

    var myGameArea = {
        canvas: document.createElement("canvas"),
        start: function () {
            this.canvas.width = 700;
            this.canvas.height = 504;
            this.context = this.canvas.getContext("2d");
            // document.body.insertBefore(this.canvas, document.body.childNodes[0]);
            // console.log(document.getElementById("minigame"))
            // console.log(document.getElementById("minigame").childNodes[0])
            // console.log(document.body)
            // console.log(document.body.childNodes[0])
            document.getElementById("minigame").insertBefore(this.canvas, document.getElementById("minigame").childNodes[0]);
            this.frameNo = 0;
            this.interval = setInterval(updateGameArea, 20);
        },
        clear: function () {
            this.context.clearRect(0, 0, this.canvas.width, this.canvas.height);
        }
    }

    function objectENG(width, height, color, x, y, type) {
        this.type = type;
        this.score = 0;
        this.width = width;
        this.height = height;
        this.speedX = 0;
        this.speedY = 0;
        this.x = x;
        this.y = y;
        this.gravity = 0;
        this.gravitySpeed = 0;
        this.update = function () {
            let ctx = myGameArea.context;
            if (this.type === "text") {
                ctx.font = this.width + " " + this.height;
                ctx.fillStyle = color;
                ctx.fillText(this.text, this.x, this.y);
            } else {
                ctx.fillStyle = color;
                ctx.fillRect(this.x, this.y, this.width, this.height);
            }
        }
        this.newPos = function () {
            this.gravitySpeed += this.gravity;
            this.x += this.speedX;
            this.y += this.speedY + this.gravitySpeed;
            // console.log(this.y)
            if (this.y < 0) {
                this.y = 0;
                this.gravitySpeed = 0;
            }
            this.hitBottom();
        }
        this.hitBottom = function () {
            var rockbottom = myGameArea.canvas.height - this.height;
            if (this.y > rockbottom) {
                this.y = rockbottom;
                this.gravitySpeed = 0;
            }
        }
        this.crashWith = function (otherobj) {
            var myleft = this.x;
            var myright = this.x + (this.width);
            var mytop = this.y;
            var mybottom = this.y + (this.height);
            var otherleft = otherobj.x;
            var otherright = otherobj.x + (otherobj.width);
            var othertop = otherobj.y;
            var otherbottom = otherobj.y + (otherobj.height);
            var crash = true;
            if ((mybottom < othertop) || (mytop > otherbottom) || (myright < otherleft) || (myleft > otherright)) {
                crash = false;
            }
            return crash;
        }
    }

    function updateGameArea() {
        var x, height, gap, minHeight, maxHeight, minGap, maxGap;
        for (let i = 0; i < myObstacles.length; i += 1) {
            if (myGamePiece.crashWith(myObstacles[i])) {
                return;
            }
        }
        myGameArea.clear();
        myGameArea.frameNo += 1;
        if (myGameArea.frameNo === 1 || everyinterval(150)) {
            x = myGameArea.canvas.width;
            minHeight = 50;
            maxHeight = 300;
            height = Math.floor(Math.random() * (maxHeight - minHeight + 1) + minHeight);
            minGap = 100;
            maxGap = 200;
            gap = Math.floor(Math.random() * (maxGap - minGap + 1) + minGap);
            myObstacles.push(new objectENG(10, height, "green", x, 0));
            myObstacles.push(new objectENG(10, x - height - gap, "green", x, height + gap));
        }
        for (let i = 0; i < myObstacles.length; i += 1) {
            myObstacles[i].x += -1;
            myObstacles[i].update();
        }
        myScore.text = "SCORE: " + myGameArea.frameNo;
        myScore.update();
        myGamePiece.newPos();
        myGamePiece.update();

        if (myGameArea.frameNo > 4000 && props.won === false) {
            // setScore(myGameArea.frameNo)
            props.setWon(1)
            props.setGameOn(false)
        }
        if (myGameArea.frameNo > 3000 && props.won === 1) {
            // setScore(myGameArea.frameNo)
            props.setWon(2)
            props.setGameOn(false)
        }
    }

    // const [score, setScore] = useState(0)

    function everyinterval(n) {
        if ((myGameArea.frameNo / n) % 1 === 0) { return true; }
        return false;
    }

    function accelerate(n) {
        myGamePiece.gravity = n;
    }

    // useEffect(() => {
    //     console.log("start game")
    //     startGame()
    // }, [])

    return (
        <div className='mini_main' id="minigame">

            <button
                className=''
                onMouseDown={() => {
                    accelerate(-0.2)
                }}
                onMouseUp={() => {
                    accelerate(0.05)
                }}
            >
                Up
            </button>

        </div>
    )
}
