import React, { useState, useEffect } from 'react'
import "./Canard.css"
import Minigame from './Minigame'

export default function Canard(props) {


    const [gameOn, setGameOn] = useState(false)
    const [won, setWon] = useState(false)

    // useEffect(() => {
    //     console.log("change")
    //     if (won === 1) {
    //         // setGameOn(true)

    //         // setTimeout(() => {
    //             // setGameOn(true)
    //             // console.log("okok")
    //         // }, 2000)
    //     }

    // }, [won])

    // useEffect(() => {
    //     console.log(won)

    // }, [won])
    // useEffect(() => {
    //     console.log(gameOn)

    // }, [gameOn])

    return (
        <div className='canard_main1'>
            <div className='canard_main2'>


                <div
                    className='minigame'
                >
                    {/* {won === 1 && <p>lvl 2</p>} */}
                    {won === 1 && !gameOn && <p className='canard_txt1'>Bien joué, vous avez fini le niveau 1 !</p>}
                    {won === 2 && <p className='canard_txt2'>Yeahhhh ! Vous voici arrivée page 64 !! Bonne route ;)</p>}
                    {gameOn && < Minigame setWon={setWon} setGameOn={setGameOn} won={won} />}
                </div>


                {won !== 2 && <button
                    className='btn_start'
                    onClick={() => {

                        if (!gameOn && won === false) {
                            setGameOn(true)
                        } else if (!gameOn && won === 1) {
                            setGameOn(true)
                        } else {
                            setGameOn(false)

                            setTimeout(() => {
                                setGameOn(true)
                            }, 100)

                        }

                    }}

                >{!gameOn ? won === false ? "Nouvelle Partie" : "Niveau 2" : "Recommencer"}
                </button>
                }

            </div>
            <button
                className='btn_retour_canard'
                onClick={() => {
                    props.setToggleMissionOne(false)
                    props.setPanneauValue("garage")
                }}
            >
                Retour
            </button>
        </div >
    )
}
